* {
    padding: 0;
    margin: 0 auto;
    list-style: none;
}
h1 {
    margin: 10px 0 10px 0;
}
h2 {
    margin-bottom: 20px;
}
.loading {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #f3f3f3;
}
.runtime {
    font-size: 24px;
    margin-bottom: 20px;
}
.img {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.desc {
    width: 80%;
    margin-bottom: 20px;
    text-align: center;
    word-break: keep-all;
}
li {
    text-align: center;
    font-weight: bold;
}
